/* src/pages/People.css */
.people-container {
  padding: 40px 20px; /* Padding around the page */
  margin: 0 auto; /* Center the container */
  max-width: 1200px; /* Maximum width for content */
}

/* Team Grid to display cards */
.team-grid {
  display: flex; /* Flexbox layout */
  flex-wrap: wrap; /* Wrap the cards */
  justify-content: center; /* Center the cards horizontally */
  gap: 30px; /* Gap between cards */
  padding: 20px; /* Padding around the grid */
  margin: 0 auto; /* Center the grid */
  max-width: 1000px; /* Maximum width for the grid */
}
/* Main container for team member cards */
.team-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden; /* Hide overflow */
  transition: transform 0.2s;
  display: flex;
  flex-direction: column; /* Ensure content is arranged vertically */
  align-items: center; /* Center content horizontally */
  width: 250px; /* Set a fixed width for the card */
  margin: 20px; /* Add margin for spacing */
}

/* Image container */
.team-card-image-container {
  width: 100%; /* Full width */
  padding-top: 100%; /* Create a square container */
  position: relative; /* Position relative for absolute positioning of image */
  overflow: hidden; /* Hide overflow */
  display: flex; /* Flexbox for centering */
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

/* Image styling */
.team-card-image {
  width: 100%; /* Fill the width */
  height: auto; /* Adjust height to maintain aspect ratio */
  position: absolute; /* Position absolute to fill the container */
  top: 0; /* Align to top */
  left: 0; /* Align to left */
  object-fit: contain; /* Fit image within container without cropping */
}

/* Team card body */
.team-card-body {
  padding: 15px; /* Padding for text content */
  text-align: center; /* Center align text */
}

.team-card-name {
  font-size: 1.2rem; /* Font size for name */
  font-weight: bold; /* Bold text for name */
  margin-bottom: 10px; /* Space below name */
}

.team-card-bio {
  font-size: 0.9rem; /* Font size for bio */
  color: #555; /* Softer text color */
  margin-bottom: 10px; /* Space below bio */
}

.team-card-role {
  font-size: 0.9rem; /* Font size for role */
  background-color: #f0f0f0; /* Background color for role */
  padding: 5px 10px; /* Padding around text */
  border-radius: 4px; /* Rounded corners */
  display: inline-block; /* Inline-block for better spacing */
  margin-top: 10px; /* Margin above the role */
}

