/* src/components/HeroSection.css */

/* Hero Section Styles */
.hero {
  background: url("../assets/hero-bg.jpg") no-repeat center center/cover; /* Correct path to the image */
  height: 50vh; /* Reduced height for a more compact look */
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff; /* Text color */
  padding: 0 20px; /* Padding for small screens */
  position: relative; /* For positioning the overlay */
  z-index: 1;
  overflow: hidden; /* Ensures no overflow on small screens */
}

/* Other styles remain unchanged */

/* Other existing styles remain the same */

.hero-content {
  max-width: 700px; /* Reduced width for content */
  margin: 0 auto; /* Center the content */
  z-index: 2; /* Above the overlay */
  padding: 30px 20px; /* Added padding for better spacing */
  background: rgba(
    0,
    0,
    0,
    0.2
  ); /* Subtle background to prevent overlapping issues */
  border-radius: 10px; /* Rounded corners */
}

.hero-content h1 {
  font-size: 3rem; /* Elegant font size */
  margin-bottom: 10px; /* Reduced space below the heading */
  line-height: 1.2; /* Line height for better readability */
  font-weight: 600; /* Slightly bold for emphasis */
  color: #ffffff; /* Pure white color for better contrast */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* Subtle shadow for readability */
}

.hero-content p {
  font-size: 1.2rem; /* Reduced subheading size */
  margin: 0 auto; /* Center text */
  line-height: 1.5; /* Line height for readability */
  color: #dddddd; /* Softer color for subheading */
  max-width: 80%; /* Limit width for readability */
  margin-top: 15px; /* Space above text */
}

